                Кодирование сигнала — это процесс преобразования дискретного сооб-щения в последовательность кодовых символов, которое осуществляется по определенному правилу. Каждому коду, то есть элементу присваивается сово-купность кодовых символов, которая имеет название кодовой комбинация, а совокупность всех кодовых комбинаций называется кодом.
Правило кодирования задают кодовой таблицей, в которой каждому со-общению соответствует определенная кодовая комбинация.
Понятие кодирования применимо только к дискретным сообщениям, по-этому для кодирования речевого сигнала - необходимо сначала представить сигнал в дискретной форме.
Рассмотрим схему передачи информации, изображенную на рисунке 1.9. Источник сообщения генерирует сообщение и передает его в преобразователь сообщения в сигнал, через кодек и модулятор получают цифровой сигнал и передают его через физическую среду, на приемной стороне приемник сигна-ла принимает сигнал и выполняются обратные преобразования цифрового сигнала в сообщения через демодулятор, декодер и преобразователь. 
