            <div v-else-if="id == 8">
                <h2>Импульсно-кодовая модуляция</h2><br>
                Импульсно-кодовая модуляция используется в системах широкополосной связи и обеспечивает скорость передачи 64 кбит/с. Также используется как предварительное звено совершенных кодеров низкоскоростной речи, так как характеристики ИКМ очень качественные.
Кодирование – это процесс преобразования, состоит он из следующих операций: 
1)	Дискретизация по времени равномерна с помощью устройства для выборки и хранения информации;
2)	Каждый отсчет сравнивается с набором опорных напряжений, то есть уровнем квантования;
3)	Наиболее близкий к уровню дискретизированного сигнала уровень квантования обнаруживается;
4)	Соответствующее найденному уровню квантованию двоичное число формируется;
5)	Вывод двоичного числа в последовательном формате;
6)	Повторение приведенных выше шагов для следующего отсчета и т.д. 
Важная характеристика ИКМ — это разница между значением дискрети-зируемого напряжения (дискретизация) и уровнями квантования, с которыми оно сравнивается. Чаще всего входные напряжения не будут соответствовать уровням квантования.
Как упоминалось выше, кодер ИКМ присваивает выборке значение бли-жайшего уровня квантования. В этом случае квантованное значение выборки отличается от фактического, и разница между ними называется ошибкой кван-тования. Это проявляется в демодуляторе ИКМ приемника, где уровень ис-ходного сигнала уже неизвестен. Вероятность и величина ошибки зависит от числа уровней квантования, то есть чем больше число уровней квантования для диапазона изменений уровня передаваемого сигнала, тем меньше погреш-ность.
Еще одной важной характеристикой систем является тактовая частота ИКМ-кодера, которая определяет частоту дискретизации. Во избежание пере-крытия спектров эта частота должна быть, по крайней мере, в два раза выше верхней частоты спектра передаваемого сообщения. Влияние такого явления, как наложение спектров на выходной сигнал системы ИКМ оставлено для рас-смотрения в операции по декодированию сигнала ИКМ.
