            <div v-else-if="id == 9">
                <h2>Дифференциальная импульсно-кодовая модуляция</h2><br>
                Для уменьшения полосы частот, нужно уменьшить разрядность кодовой комбинации, этот способ приводит к увеличению шага квантования и сниже-нию защиты сигналов от шумов квантования. 
Этот недостаток может быть устранен за счет использования корреляций между отсчетами речевых сигналов, которые являются соседними. Квантова-нию и кодированию должно подвергаться не абсолютное значение стандарта, а разница между предыдущим и последующим отсчетами исходного сигнала.
Диапазон различий между отсчетами меньше, чем сами выборки, поэтому для кодирования значения разницы при той же частоте дискретизации, что и в классическом PCM, требуется меньше битов. Таким образом, полоса частот, необходимая для его передачи, уменьшается при формировании такого циф-рового сигнала. Способ формирования цифрового сигнала, когда квантова-нию и кодированию подвергают лишь разницу между двумя соседними отсче-тами, называется дифференциальная импульсно кодовая модуляция ДИКМ. На рисунке 1.10 изображена структурная схема передатчика с ДИКМ.
<div class="image"><img src="/7.png" /></div>
В схеме для получения разности значений дискретов запоминается предыдущая входная дискрета непосредственно в памяти и используется ана-логовое вычитающее устройство для измерения изменения, далее изменение сигнала квантуется и кодируется для передачи.
ЭЗ - элемент задержки сигнала C(t) на время T, которое равно периоду дискретизации Tд, на выходе ЭЗ формируется сигнал C(t-Tд), ДУ - дифферен-циальный усилитель, который выполняет роль вычитающего устройства, на выходе которого получается разностный сигнал r(nTд), кодер - это кодирую-щее устройств, которое формирует ДИКМ цифровой сигнал, а декодер - это декодирующее устройство, которое преобразует принятый цифровой ДИКМ сигнал в отсчеты разностного сигнала, Интг - интегратор, который преобра-зует сигнал r(nTд) на выходе декодера в ступенчатый сигнал, который с по-мощью ФНЧ приема преобразуется в сигнал вида C(t), который отличается от сигнала C(г) наличием шумов квантования и присущих ДИКМ искажений.
Принимающая сторона, принимая цифровую последовательность, снача-ла декодирует ее, в результате чего сначала восстанавливается последова-тельность приращений квантованного сигнала во время подсчета.
Затем путем последовательного суммирования они преобразуются в по-следовательность выборок квантованного сигнала и затем преобразуются в исходный аналоговый сигнал.
            </div>
            <div v-else-if="id == 10">
                <h2>Адаптивная дифференциальная импульсно-кодовая модуляция</h2><br>
                Наиболее широко распространённый и проверенный временем алгоритм сжатия речи – это АДИКМ, он регулируется стандартом G.726. Данный алго-ритм обеспечивает такое же качество речи, как и RCM, но скорость передачи необходима всего 32 кбит/с.
В аналоговом сигнале, который передает речь – невозможны резки скач-ки амплитуды, поэтому можно кодировать не ее амплитуду, а ее изменение по сравнению с предыдущим значением, то есть обойтись меньшим количеством цифр. В адаптивной дифференциальной импульсно-кодовой модуляции изме-нение уровня сигнала кодируют 4-х значным числом, а частота изменения ам-плитуды сигнала не меняется. 
На рисунке 1.11 показана функциональная схема кодека с АДИКМ. На вычитающем устройстве ВУ формируется сигнал, который является разно-стью входного сигнала и суммарного предсказания и его разности, которая формируется кодеком. Кодек АДИКМ — это замкнутая система с цепью об-ратной связи, в которую формируются из предыдущих отсчетов входного сигнала в ПРДС, которые поступают на сумматор. На второй вход сумматора поступает разностный сигнал, который формируется схемой сравнения СС, блоком управления БУ, нелинейным цифровым преобразователем НЦП, ко-торый представляет экспандер с коэффициентом экспандирования, который изменяется под воздействием сигналов от блока управления БУ.
<div class="image"><img src="/8.png" /></div>
Процесс адаптации осуществляется с изменениями крутизны входного сигнала, то есть меняется шаг квантования. Формирование сигнала происхо-дит с такой величиной и знаком, чтобы точность была до ошибки квантова-ния.
Процесс взвешивания контролируется схемой сравнения СС, второй вход которой имеет нулевой потенциал, формирователь кода ФК формируется ко-довую комбинацию, которая соответствует разностному сигналу. Приемная сторона преобразователя воспроизводит обратные действия, но только пре-образователь кода ПК формирует цифровую комбинацию, которая соответ-ствует разностному сигналу в параллельном коде.
Кодеры, работающие на основе ДИКМ, работают так, "думая", что вы-ходной сигнал источника сообщения является стационарным, но самом деле речь является случайным нестационарным процессом, так как спектр, диспер-сия и функция автокорреляции меняются во времени.
Для эффективного кодирования данные изменения постоянно отслежива-ются. Данная мера сводит к минимальному динамическому диапазону ошибок для прогнозирования и число уровней квантования.
            </div>
            <div v-else-if="id == 11">
                <h2>Сравнение кодеков</h2><br>
                Передача данных происходит различными путями, в зависимости от типа используемого оборудования. Например, GSM функционирует при подклю-чениях GSM и VoIP, а G.711 применяется для устройств IDSN и используется системой как первичный цифровой канал в совместном применении с SIP и SCCP. 
Наиболее популярным кодеком в телефонии является кодек стандарта G.711, его пропускная способность 64 кбит/сек, и он поддерживается практи-чески всеми устройствами IP телефонии.
Кодек G.729 предназначен для передачи речи с хорошим качеством и ис-пользую небольшую пропускную способность - 8 кбит / сек. По субъективным оценкам, данный кодек обладает лучшим качеством, чем у G.723, но худшим, чем у G.711.Поддерживается практически всеми производителями оборудо-вания и при коммерческом использовании требуется лицензия. Кодек G.723 активно используется в телефонии, имеет очень низкую скорость потока 5.3-6.3 кбит / сек. По некоторым оценкам имеет худшее качество речи среди рас-сматриваемых кодеков, поддерживается значительной частью устройств IP те-лефонии.
Кодеки стандарта GSM разработаны для использования в системах сото-вой связи стандарта GSM, при кодировании кадра используют предыдущий кадр, поддерживается производителями оборудования, в основном в шлюзах между сотовыми и VoIP сетями.
Кодеки стандарта iLBC открытые, имеют скорость потока 13.3 кбит/сек, по некоторым оценкам экспертов имеет качество речи лучше, чем у G.729А, кодек устойчив к потере кадров, используется, например в сети телефонии Skype.
На основании вышеописанной информации и некоторых оценок экспер-тами, можно расположить кодеки так: 
1)	По порядку ухудшению качества: G.711, iLBC, G.729, GSM, G.723.
2)	По порядку пропускной способности: G.723, iLBC, G.729, GSM, G.711.

