<template>
  <div v-if="test != null" class="container content">
    <div class="row justify-content-md-center">
      <div class="col-md-6 col-12 col-sm-12 card">
        <div class="card-body">

        <h1>{{ test.name }}</h1>
        <div v-if="timer && currentQuestion > 0">{{time}}</div>
        <div v-if="currentQuestion == 0">
          <div>Для прохождения теста - введите свои данные</div>
          <div class="form-group">
            <label for="name" class="form-label mt-4">Фамилия Имя</label>
            <input
              type="text"
              class="form-control"
              id="name"
              placeholder="Иванов Иван"
              v-model="name"
            />
          </div>
          <div class="form-group">
            <label for="group" class="form-label mt-4">Группа</label>
            <input
              type="text"
              class="form-control"
              id="group"
              placeholder="921"
              v-model="group"
            />
          </div>
          <div class="form-group" style="margin: 15px 0">
            <div class="form-check form-switch">
              <input class="form-check-input" type="checkbox" id="flexSwitchCheckChecked" v-model="timer">
              <label class="form-check-label" for="flexSwitchCheckChecked">На оценку</label>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-end">
            <button
              class="btn btn-primary"
              style="margin-top: 15px"
              @click="generateQuestion()"
            >
              Начать тест
            </button>
          </div>
        </div>

        <div v-else>
          <h3 v-if="currentQuestion > 0">{{ test.questions[currentQuestion - 1].text }}</h3>

          <div v-if="currentQuestion == -1000 && timer"><h3 style="display: inline">{{name}}</h3> из группы <i>{{group}}</i><br> Тест пройден. Набрано баллов: <b>{{success}}</b> из <b>{{success+not_success}}</b>. Ваша оценка: <h3 style="display: inline">{{genOc()}}</h3></div>
          <div v-if="currentQuestion == -1000 && !timer"><h3 style="display: inline">{{name}}</h3> из группы <i>{{group}}</i><br> Тест пройден. Набрано баллов: <b>{{success}}</b> из <b>{{success+not_success}}</b></div>
          <div v-else-if="currentQuestion > 0">
            <div v-if="test.questions[currentQuestion - 1].type == 'once'">
              <fieldset class="form-group">
                <legend class="mt-2">Выберите один вариант ответа</legend>
                <div
                  v-for="(answer, i) in test.questions[currentQuestion - 1]
                    .answers"
                  :key="'answer' + i"
                  class="form-check"
                >
                  <label class="form-check-label">
                    <input
                      type="radio"
                      class="form-check-input"
                      name="optionsRadios"
                      :value="i"
                    />
                    {{ answer.text }}
                  </label>
                </div>
              </fieldset>
            </div>
            <div v-if="test.questions[currentQuestion - 1].type == 'type_1'">
              <fieldset class="form-group">
                <legend class="mt-2">
                  Перетаскивайте вверх-вниз блоки справа, чтобы установить
                  соответствие между термином и его определением.
                </legend>
                <div style="display: flex; justify-content: space-between">
                  <div class="terms">
                    <div
                      v-for="answer in type_1.terms"
                      :key="'term' + answer.id"
                    >
                      {{ answer.text }}
                    </div>
                  </div>
                  <draggable
                    class="definitions"
                    v-model="type_1.definitions"
                    group="text"
                    @start="drag = true"
                    @end="drag = false"
                    item-key="id"
                  >
                    <template #item="{ element }">
                      <div>{{ element.text }}</div>
                    </template>
                  </draggable>
                </div>
              </fieldset>
            </div>
            <div v-if="test.questions[currentQuestion - 1].type == 'type_2'">
              <fieldset class="form-group">
                <legend class="mt-2">
                  Перетаскивайте блоки с нижней части в блоки с определением
                </legend>
                <div style="display: flex; justify-content: space-between">
                  <div class="type-2-terms">
                    <div
                      v-for="(answer, i) in test.questions[currentQuestion - 1].answers"
                      :key="'term' + answer.id"
                      class="term"
                    >
                      <span class="term-header">{{ answer.term }}</span>
                      <div class="area" @drop="(e) => { onDrop(e, i) }" @dragenter.prevent @dragover.prevent>
                        <div v-for="(item, j) in answer.drops" :key="'drop' + i + j" class="term-drop">
                          <span>{{item.text}}</span><button class="close" @click="deleteAreaChild(i, j)"></button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="type-2-definition">
                    <div draggable="true" @dragstart="(e) => {dragStart(e, i)}" @dragmove="dragMove" v-for="(child, i) in type_2.childs" :key="'child' + i" v-show="child.status" class="child">{{child.text}}</div>
                  </div>
                </div>
              </fieldset>
            </div>
            <div v-if="test.questions[currentQuestion - 1].type == 'type_3'">
              <fieldset class="form-group">
                <legend class="mt-2">
                  Перетаскивайте вверх-вниз блоки, чтобы установить правильную последовательность
                </legend>
                <div style="display: flex; justify-content: space-between">
                  <draggable
                    class="type-3-answers"
                    v-model="type_3.answers"
                    group="text"
                    @start="drag = true"
                    @end="drag = false"
                    item-key="id"
                  >
                    <template #item="{ element }">
                      <div>{{ element.text }}</div>
                    </template>
                  </draggable>
                </div>
              </fieldset>
            </div>
            <div v-if="test.questions[currentQuestion - 1].type == 'type_4'">
              <fieldset class="form-group">
                <legend class="mt-2">
                  Кликайте на поля ввода для выбора пропущенного слова
                </legend>
                <div class="type-4-text" v-html="type_4.text">
                </div>
              </fieldset>
            </div>
            <div style="display: flex; justify-content: space-between;width: 100%;margin-top: 10px;">
              <button :style="currentQuestion > 1 ? '' : 'visible: hidden'" class="btn btn-info" @click="generateQuestion(false)">
                Назад
              </button>
              <button class="btn btn-info" @click="generateQuestion()">
                Далее
              </button>
            </div>
          </div>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "./Test.css";

import draggable from "vuedraggable";
import moment from 'moment';
import jsondata from './test.json'

export default {
  components: { draggable },
  data() {
    return {
      test: null,
      name: "",
      group: "",
      currentQuestion: 0,
      type_1: null,
      type_2: null,
      type_3: null,
      type_4: null,
      drag: false,
      dragType2ChildCell: null,
      timer: false,
      date: moment(60 * 10 * 1000),
      success: 0,
      not_success: 0
    };
  },
  mounted() {
    
        this.test = jsondata;

      document.querySelector('body').addEventListener('click', (event) => {
        if(event.path[0].classList[0] == 'select') {
          if(document.querySelector('.select[data-id="'+id+'"] .select-area') != null)
          document.querySelector('.select[data-id="'+id+'"] .select-area').remove();
          var words = this.test.questions[this.currentQuestion - 1].words;
          var id = event.path[0].getAttribute('data-id')
          var html = `<div class="select-area">`;

          for(var i = 0; i < words.length; i++) {
            html += `<div class="select-item" data-id="`+words[i].id+`">` + words[i].text + `</div>`;
          }

          html += `</div>`;
          document.querySelector('.select[data-id="'+id+'"]').insertAdjacentHTML('beforeend', html)
        }

        if(event.path[0].classList[0] == 'select-item') {
          event.path[0].parentElement.parentElement.setAttribute('selected-id', event.path[0].getAttribute('data-id'))
          event.path[0].parentElement.parentElement.innerHTML = event.path[0].innerText
          event.path[0].parentElement.parentElement.querySelector('.select-area').remove()
        }
      })
  },
  computed: {
    time: function(){
      return this.date.format('mm:ss');
    }
  },
  methods: {
    genOc() {
      var ocenka = 2;
      var all = this.success + this.not_success;
      var coff = this.success / all * 100;

      if(coff >= 80) ocenka = 5;
      else if(coff >= 65) ocenka = 4;
      else if(coff > 40) ocenka = 3;

      return ocenka;
    },
    deleteAreaChild(i, j) {
      var drop = this.test.questions[this.currentQuestion - 1].answers[i].drops[j]
      for(var c = 0; c < this.type_2.childs.length; c++) {
        if(drop.id == this.type_2.childs[c].id && drop.term_id == this.type_2.childs[c].term_id) {
          this.type_2.childs[c].status = true
        }
      }
      this.test.questions[this.currentQuestion - 1].answers[i].drops.splice(j, 1)
    },
    dragStart(e, i) {
      console.log('dragStart')
      this.dragType2ChildCell = i
    },
    dragMove(e) {
      console.log('dragMove')
    },
    onDrop(e, i) {
      e.preventDefault()
      var j = this.dragType2ChildCell
      console.log(this.test.questions[this.currentQuestion - 1].answers[i])
      this.type_2.childs[j].status = false
      this.test.questions[this.currentQuestion - 1].answers[i].drops.push(this.type_2.childs[j])
      console.log('onDrop')
    },
    checkQuestion() {
      if(this.currentQuestion == 0) return 1;
      var question = this.test.questions[this.currentQuestion - 1];
      if(question.type == 'type_1') {
        var errors = 0;
        for(var i = 0; i < this.type_1.definitions.length; i++) {
          if(this.type_1.definitions[i].id != i) {
            errors += 1;
          }
        }

        if(errors > 0) console.log('errors ' + errors)
        else console.log('norm')

        this.success += this.type_1.definitions.length - errors
        this.not_success += errors
      }

      if(question.type == 'type_2') {
        var all = 0;
        var success = 0;
        for(var i = 0; i < question.answers.length; i++) {
          all += question.answers[i].childs.length
          for(var j = 0; j < question.answers[i].childs.length; j++) {
            var flag = false;
            for(var c = 0; c < question.answers[i].drops.length; c++) {
              if(question.answers[i].childs[j] == question.answers[i].drops[c].text) {
                flag = true;
                break;
              }
            }
            if(flag) success += 1;
            else
            this.not_success += 1
          }
        }
        this.success += success
      }

      if(question.type == 'type_3') {
        var balls = 0;
        for(var i = 0; i < question.answers.length; i++) {
          if(this.type_3.answers[i].id == i + 1) balls += 1;
          else 
        this.not_success += 1
        }
        this.success += balls
      }

      if(question.type == 'type_4') {
        var balls = 0;

        var els = document.querySelectorAll('.select')

        for(var i = 0; i < els.length; i++) {
          console.log(els[i].getAttribute('selected-id'), els[i].getAttribute('data-correct'), els[i].getAttribute('selected-id') == els[i].getAttribute('data-correct'))
          if(els[i].getAttribute('selected-id') != null && els[i].getAttribute('selected-id') == els[i].getAttribute('data-correct')) {
            balls += 1;
          } else 
        this.not_success += 1
        }
        this.success += balls
      }
    },
    generateQuestion(state = true) {
      if(this.currentQuestion < 0) return 1;
      if(this.currentQuestion == 0) {
        setInterval(() => {
          this.date = moment(this.date.subtract(1, 'seconds'))
        }, 1000);
      }
      this.checkQuestion();
      if(state == false) {
        this.currentQuestion -= 1;
      } else this.currentQuestion += 1;
      if(this.currentQuestion > this.test.questions.length) {
        this.currentQuestion = -1000
        return 1;
      }
      var question = this.test.questions[this.currentQuestion - 1];

      if (question.type == "type_1") {
        this.type_1 = { terms: [], definitions: [] };

        var indexes = [];
        for (var i = 0; i < question.answers.length; i++) {
          indexes.push(i);
        }

        var terms = this.shuffle(indexes);

        var indexes = [];
        for (var i = 0; i < question.answers.length; i++) {
          indexes.push(i);
        }

        var definitions = this.shuffle(indexes);

        for (var i = 0; i < terms.length; i++) {
          var tmp = {};
          tmp.id = terms[i];
          tmp.text = question.answers[terms[i]].term;
          this.type_1.terms.push(tmp);
        }

        for (var i = 0; i < definitions.length; i++) {
          var tmp = {};
          tmp.id = definitions[i];
          tmp.text = question.answers[definitions[i]].definition;
          this.type_1.definitions.push(tmp);
        }
      }

      if(question.type == 'type_2') {
        this.type_2 = {childs: []}

        for(var i = 0; i < question.answers.length; i++) {
          for(var j = 0; j < question.answers[i].childs.length; j++) {
            this.type_2.childs.push({term_id: i, id: j, text: question.answers[i].childs[j], status: true})
          }
        }
        this.type_2.childs = this.shuffle(this.type_2.childs)
      }
      if(question.type == 'type_3') {
        this.type_3 = {answers: []}

        for(var i = 0; i < question.answers.length; i++) {
          this.type_3.answers.push(question.answers[i])
        }
        this.type_3.answers = this.shuffle(this.type_3.answers)
      }
      
      if(question.type == 'type_4') {
        this.type_4 = {text: ''}
        var maxLength = 0;
        var maxText = '';
        for(var i = 0; i < question.words.length; i++) {
          if(question.words[i].text.length > maxLength) {
            maxLength = question.words[i].text.length
            maxText = question.words[i].text
          }
        }
        maxLength = maxLength * 10 + 'px'
        this.type_4.text = this.replaceAll(question.answer, /{(\d+)\.(\d+)}/, "<div class='select' data-id='$1' data-correct='$2' style='width: "+maxLength+"'></div>")
      }

    },
    replaceAll(str, find, replace) {
      return str.replace(new RegExp(find, 'g'), replace);
    },
    shuffle(array) {
      var currentIndex = array.length,
        temporaryValue,
        randomIndex;

      while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }

      return array;
    },
  },
};
</script>