                <h1>Кодеки стандарта GSM</h1><br>
                1.11.1 Стандарт GSM. GSM (Глобальная система мобильной связи) — это глобальный стандарт цифровой мобильной сотовой связи с разделением ка-налов по частоте и времени. GSM и разработка начались в начале 80-х годов прошлого века группой из 26 европейских телефонных компаний. Предпола-галось, что этот стандарт объединит все европейские страны в систему сото-вой связи с диапазоном 900 МГц.
К концу 80-х годов дальнейшим развитием GSM занимался Европейский институт стандартизации телекоммуникаций (ETSI). Спустя несколько лет сво-ей деятельности институт опубликовал первые рекомендации для будущих операторов мобильной связи.
С 1991 года в европейских странах работают первые коммерческие сети GSM. Она относится ко второму поколению сетей стандарта GSM (2 g), по-скольку была разработана позже аналоговой сотовой связи (1 g) и во многих отношениях была лучше спроектирована.
1.11.2 Стандарт Full Rate. Первый стандарт цифрового кодирования ре-чи, используемый в телефонах GSM. Битрейт кодека составляет 13 кбит/с. Ка-чество звука очень низкое по сравнению с современными стандартами, но в начале 1990-х годов, когда он разрабатывался, кодек FR был хорошим ком-промиссом между сложностью реализации и качеством звука.
