1.11.3 Стандарт Enhanced Full Rate. Стандартизированный алгоритм цифрового кодирования голоса в широкополосном GSM, а также кодек, реа-лизованный на его основе. Это продолжение разработки алгоритма полной скорости (FR), разработанного на его основе.
