                G.726 — это стандарт речевого кодека адаптивной дифференциальной импульсно-кодовой модуляции, охватывающий передачу голоса на скоростях 16, 24, 32 и 40 кбит/с. Он был введен для замены как G.721, описывающего АДИКМ на 32 кбит/с, так и G.723, описывающего АДИКМ на 24 и 40 кбит/с. G.726 также представил новую скорость 16 кбит/с. Четыре скорости передачи данных, связанные с G.726, часто обозначаются битовым размером выборки, который составляет 2, 3, 4, 5 бит соответственно.
