Он в основном используется на международных магистралях телефонной сети и является стандартным кодеком, используемым в беспроводных теле-фонных системах DECT. 
Основное применение каналов 24 и 16 кбит/с – для каналов перегрузки передачи речи в цифровом оборудовании умножения каналов (ДИКМ). Они Каналы 40 кбит/c применяются для передачи модемного сигнала на ДИКМ. Особенно для модемов, работающих на скоростях более 4800 бит/с.
G.726 соответствует рекомендации ITU-T G.726, определяющей сжатие и распаковку речи на скоростях 16, 24, 32 и 40 Кбит/с на основе адаптивной дифференциальной импульсно-кодовой модуляции.
